
:root {
	--bgColor: #0d686d;
	--containerColor: #119da4;
}
.container {
	background: var(--containerColor);
	height: 100%;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	text-align: left;
}
.inner-container {
	width: 25%;
}
#app {
	background: var(--bgColor);
	height: calc(100vh - 60px);
	padding: 30px;
	-webkit-transition: all 1.3s;
	transition: all 1.3s;
}
#details {
	margin-top: 24px;
	font-size: 18px;
	text-align: justify;
}
body {
	margin: 0px;
	padding: 0px;
	font-family: 'Circular Book', 'Arial';
	color: rgba(255, 255, 255, 0.8);
}
* {
	margin: 0px;
	padding: 0px;
}
@font-face {
	font-family: 'Circular Bold';
	font-style: normal;
	font-weight: normal;
	src: local('Circular Bold'),
		url(/static/fonts/lineto-circular-pro-bold.1e79ac9.woff) format('woff');
}
@font-face {
	font-family: 'Circular Book';
	font-style: normal;
	font-weight: normal;
	src: local('Circular Book'),
		url(/static/fonts/lineto-circular-pro-book.ffdf584.woff) format('woff');
}
@media screen and (max-width: 576px) {
.inner-container {
		width: 90%;
}
#details {
		font-size: 15px;
}
#app {
		height: calc(100vh - 32px);
		padding: 16px;
}
}

